import { bindActionCreators } from 'redux';
import {

  add_product_to_cart,
} from './actionCreators/actionsCreator';

function mapDispatchToProps(component) {
  switch (component) {
    case 'Main':
      return function (dispatch) {
        return {
          add_product_to_cart: bindActionCreators(
            add_product_to_cart,
            dispatch
          ),
        };
      };

    default:
      return undefined;
  }
}

export default mapDispatchToProps;
